/* Page Styles */
body {
  padding: 0;
  margin: 0;
  width: 100vh;
  height: 300vh;
  background-color:black;
  /* background-image: url("./images/BG.png");
  background-size: cover; */
  color: white;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
a.name:link, a.name:visited {
  color: #e51b88;

}
a.name:hover {
  color: white;
  font-size: xx-large;
}
a:link, a:visited{
  color: white;
}
a:hover {
  color: #e51b88;
  font-size: large;
}
.font-link {
  font-family: "Permanent Marker", cursive;
}
.bg{
  width: 100vw;
  z-index: index -999;
  position: absolute;
}
/* Nav Bar Styles */
.header {
  width: 90vw;
  height: 12vh;
  padding: 5px;
  padding-left: 5vw;
  padding-right: 5vw;
  position: fixed;
  font-size: small;
  /* background-color: black; */
  display: inline-grid;
  grid-template-columns: 1fr 2fr;
  justify-content: space-between;
  z-index:99
}
.navigation {
  display: inline-grid;
  grid-template-columns: repeat(4,1fr);
  /* display:flex; */
  width: 50vw;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  justify-self: end;
  color: white;
}

/* Landing/Intro Component Styles */
.MananFormal {
  width: 30vw;
  height: auto;
  position: absolute;
  top: 50%;
  right: 25%;
  z-index:-1;
  transform: translate(50%,-50%);
}
.landing {
  width: 100vw;
  /* max-width: 100%; */
  height: 100vh;
  margin: 0;
  /* padding-left: 10vw;
  padding-right: 10vw; */
  /* position: relative; */
  /* background-color: black; */
}
.greeting {
  width: 70vw;
  max-width: 100%;
  font-size: 120px;
  font-size: 12vw;
  line-height: 50px;
  line-height: 12vw;
  color: white;
  position: absolute;
  top: 50%;
  left: -25%;
  transform: translate(50%,-50%);
  overflow: visible;
}
.name {
  color: #e51b88;
}
/* About section styles */
.about {
  z-index: 1;
  width: 100vw;
  height: 750px;
}
